.pdfViewBg {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
  background: rgba(0, 0, 0, 0.7);
  z-index: 999;
}
.pdfView {
  position: fixed;
  top: 10vh;
  left: 10vw;
  /* margin: 0 auto; */
  width: 80vw;
  height: 85vh;
  background-color: white;
  overflow: scroll;
}
.viewer {
  width: 95%;
  height: 90%;
}
.pdfViewClose {
  text-align: right;
  padding: 0;
}
.pdfViewCol {
  text-align: center;
}
